import { Component, OnInit, Input, Renderer } from '@angular/core';

@Component({
  selector: 'app-layers',
  templateUrl: './layers.component.html',
  styleUrls: ['./layers.component.scss']
})
export class LayersComponent implements OnInit {
  @Input() layers;

  selected: any;

  constructor(private renderer: Renderer) { }

  ngOnInit() { }

  select(event, layer) {
    event.target.parentElement.classList.toggle('open');

    if (layer['<class>'] != 'group') {
      event.target.parentElement.classList.toggle('selected');
    }

    console.log(layer);
  }

  layerInformation(layer) {
    let className: string;
    let visible: boolean = true;

    switch (layer['_class']) {
      case ('page'):
        className = 'page';
        break;
      case ('artboard'):
        className = 'artboard';
        break;
      case ('group'):
        className = 'group';
        break;
      case ('text'):
        className = 'text';
        break;
      case ('bitmap'):
        className = 'image';
        break;
      case ('shapeGroup'):
        switch (layer.layers[0]['_class']) {
          case ('rectangle'):
            className = 'rectangle';
            break;
          case ('oval'):
            className = 'oval';
            break;
          case ('star'):
            className = 'star';
            break;
          case ('triangle'):
            className = 'triangle';
            break;
          case ('shapePath'):
            className = 'vector';
            break;
        }
        break;
      case ('rectangle'):
        className = 'rectangle';
        visible = false;
        break;
      case ('oval'):
        className = 'oval';
        visible = false;
        break;
      case ('star'):
        className = 'star';
        visible = false;
        break;
      case ('triangle'):
        className = 'triangle';
        visible = false;
        break;
      case ('shapePath'):
        className = 'vector';
        visible = false;
        break;
    }

    return {'classname': className, 'visible': visible};
  }

  getClassName(layer) {
    return this.layerInformation(layer).classname;
  }

  isVisible(layer) {
    return this.layerInformation(layer).visible;
  }

}



