import { Component, OnInit, Input, Renderer } from '@angular/core';
import { FileService } from '../services/file.service';

@Component({
  selector: 'app-tree-view',
  templateUrl: './tree-view.component.html',
  styleUrls: ['./tree-view.component.scss']
})
export class TreeViewComponent implements OnInit {
  @Input() layers;
  @Input() type;

  selected: any;

  constructor(
    public renderer: Renderer,
    public fileService: FileService
  ) { }

  ngOnInit() { }

  selectLayer(event, layer) {
    event.target.parentElement.classList.toggle('open');

    if (layer['_class'] !== 'group') {
      // Remove all selected classes from every layer
      const layers = document.querySelectorAll('.sidebar__box.layers li');

      [].forEach.call(layers, function(el) {
        el.classList.remove('selected');
      });

      // Remove all highlight classes from every highlight
      const hotspots = document.querySelectorAll('.main .hotspot');

      [].forEach.call(hotspots, function(el) {
        el.classList.remove('highlight');
      });

      // Add selected class to selected layer
      event.target.parentElement.classList.toggle('selected');

      // Add highlight class to selected hotspot
      this.hightlightHotspot(false, layer, true);

      this.fileService.selectedLayer = layer;
    }
  }

  selectHotspot(event, layer) {
    event.stopPropagation();

    console.log(layer);

    // Remove all selected classes from every layer
    const layers = document.querySelectorAll('.sidebar__box.layers li');

    [].forEach.call(layers, function(el) {
      el.classList.remove('selected');
    });

    // Select layer in layerlist
    const objectID = layer['do_objectID'];
    const sidebarLayer = document.body.querySelector('.sidebar__left [data-objectid=\'' + objectID + '\']');
    sidebarLayer.classList.add('selected');
    $('.sidebar__left [data-objectid=\'' + objectID + '\']').parents('.group').addClass('open');

    // Remove all highlight classes from every highlight
    const hotspots = document.querySelectorAll('.main .hotspot');

    [].forEach.call(hotspots, function(el) {
      el.classList.remove('highlight');
    });

    this.hightlightHotspot(false, layer, true);
    this.fileService.selectedLayer = layer;
  }

  hightlightHotspot(event, layer, hightlight) {
    const objectID = layer['do_objectID'];
    const hotspot = document.body.querySelector('.main [data-objectid=\'' + objectID + '\']');

    if (hightlight) {
      hotspot.classList.add('highlight');
    } else {
      if (event) {
        if (!event.target.parentElement.classList.contains('selected')) {
          hotspot.classList.remove('highlight');
        }
      }
    }
  }

  layerInformation(layer) {
    let classname: string = layer['_class'];
    let visible: boolean = true;

    if (layer['_class'] === 'shapeGroup') {
      classname = layer.layers[0]['_class'];
    }

    const hiddenElements = ['rectangle', 'oval', 'star', 'triangle', 'shapePath'];

    if (hiddenElements.indexOf(layer['_class']) > -1) {
      visible = false;
    }

    return {'classname': classname, 'visible': visible};
  }

  getClassname(layer) {
    return this.layerInformation(layer).classname;
  }

  isVisible(layer) {
    return this.layerInformation(layer).visible;
  }

  getStyle(layer, idx) {
    let styles;

    if (layer['_class'] == 'page') {
      styles = {
        'overflow': 'visible',
        'width': '99999px'
      }
    } else if (layer['_class'] == 'artboard') {
      styles = {
        'position': 'relative',
        'overflow': 'hidden',
        'float': 'none',
        'margin': '50px auto',
        'width': layer.frame.width + 'px',
        'height': layer.frame.height + 'px',
      }
    } else {
      styles = {
        'position': 'absolute',
        'overflow': 'hidden',
        'width': layer.frame.width + 'px',
        'height': layer.frame.height + 'px',
        'top': layer.frame.y + 'px',
        'left': layer.frame.x + 'px',
        'z-index': 9999 - idx
      }
    }

    return styles;
  }

  getImageStyle(layer) {
    let styles;

    const X = layer.frame.x - this.fileService.XMin;
    const Y = layer.frame.y - this.fileService.YMin;
    const width = this.fileService.data['imageData_width'];
    const height = this.fileService.data['imageData_height'];

    styles = {
      'width': width + 'px',
      'height': height + 'px',
      'object-fit': 'none',
      'object-position': '-' + X + 'px -' + Y + 'px'
    }

    return styles;
  }
}



