import { Component, OnInit } from '@angular/core';

import { LayersComponent } from './layers/layers.component';
import { FileService } from '../services/file.service';

@Component({
  selector: 'app-sidebar-left',
  templateUrl: './sidebar-left.component.html',
  styleUrls: ['./sidebar-left.component.scss']
})
export class SidebarLeftComponent implements OnInit {

  artboards: any = [];
  currentArtboard: any = [];
  treetype = 'layers';

  constructor(
    public fileService: FileService
  ) { }

  ngOnInit() {

  }

  log(object) {
    console.log(object);
    console.log(this.fileService.data);
  }

  showLayersOfArtboard(event, artboard) {
    // Remove all selected classes from every artboard
    const artboards = document.querySelectorAll('.sidebar__box.artboards li, .sidebar__box.layers li');

    [].forEach.call(artboards, function(el) {
      el.classList.remove('selected');
    });

    event.target.parentElement.classList.add('selected');

    this.currentArtboard = [];
    this.currentArtboard.push(artboard.layers);
  }
}



