import { environment } from '../environments/environment';

import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import { TreeViewComponent } from './tree-view/tree-view.component';

import { HeaderComponent } from './header/header.component';

import { SidebarLeftComponent } from './sidebar-left/sidebar-left.component';
import { LayersComponent } from './sidebar-left/layers/layers.component';

import { SidebarRightComponent } from './sidebar-right/sidebar-right.component';

import { PropertiesComponent } from './properties/properties.component';
import { FillsComponent } from './properties/fills/fills.component';
import { BordersComponent } from './properties/borders/borders.component';
import { ShadowsComponent } from './properties/shadows/shadows.component';
import { DimensionsComponent } from './properties/dimensions/dimensions.component';
import { TextComponent } from './properties/text/text.component';

import { ViewerComponent } from './viewer/viewer.component';

import { DropzoneComponent } from './dropzone/dropzone.component';

import { FileService } from './services/file.service';

import { AngularFireModule } from 'angularfire2';
import { AngularFireDatabaseModule } from 'angularfire2/database';
import { AngularFireAuthModule } from 'angularfire2/auth';

import * as $ from 'jquery';
import * as JSZip from 'jszip';

import { NgPipesModule } from 'ngx-pipes';
import { HasKeyPipe } from './pipes/has-key.pipe';
import { NSColorToHexPipe } from './pipes/nscolor-to-hex.pipe';


@NgModule({
  declarations: [
    AppComponent,
    TreeViewComponent,
    HeaderComponent,
    SidebarLeftComponent,
    LayersComponent,
    SidebarRightComponent,
    PropertiesComponent,
    DimensionsComponent,
    TextComponent,
    FillsComponent,
    BordersComponent,
    ShadowsComponent,
    ViewerComponent,
    DropzoneComponent,
    HasKeyPipe,
    NSColorToHexPipe,
  ],
  imports: [
    BrowserModule,
    HttpModule,
    AngularFireModule.initializeApp(environment.firebase),
    AngularFireDatabaseModule,
    AngularFireAuthModule,
    NgPipesModule
  ],
  providers: [FileService],
  bootstrap: [AppComponent]
})
export class AppModule { }



